.parkCard {
  width: 33.333%;
}

.cardItem {
  text-align: center;
  width: 300px;
  padding: 0.5rem;
  margin: 0 auto 4rem;
  border-radius: 10px;
  box-shadow: 0px 0px 15px 0px rgb(50 50 50 / 75%);
  cursor: pointer;
  transition: all 0.2s;
}

.cardImg {
  width: 100%;
  height: 300px;
  margin: 0 auto 1rem;
}

.cardImg img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.cardItem:hover {
  transform: scale(1.05);
}
.cardItem:active {
  transform: scale(1.03);
}

.title {
  color: #000;
  font-family: semiFont;
  font-size: 1rem;
}

@media (max-width: 1000px) {
  .parkCard {
    width: 50%;
  }
}
@media (max-width: 750px) {
  .parkCard {
    width: 100%;
  }
  .cardItem {
    width: 90vw;
    padding: 1rem;
    margin: 0 auto 4rem;
  }
}